* {
    margin: 0;
}
.top-bar {
    height: 48px;
    width: 100;
    background-color:#fef7f6;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: 11px;
    font-weight: 300;
    text-transform: uppercase;
}
.contact span:first-child {
    padding-right: 15px;
    
    
}
.info span {
    text-align: right;
    padding-right: 15px;
}
.container {
    height: 90.39px;
    width: 100;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-around;
    

    }
.menu a {
   margin: 0;
   padding: 0;
   color: black;
   text-align: center;
    .icon-container {
       font-size: 18px;
       display: flex;
       flex-direction: row;
      
    }
    .card-bag-icon {
        position: relative;
        padding: 10px;
        
    }
    .circle {
        background-color: rgb(242, 69, 69);
        color: white;
        font-size: 8px;
        font-weight:400;
        position: absolute;
        top:0px;
        right: 0px;
        // border: 5px solid red;
        border-radius: 50%;
        width: 16px;
        height: 16px;
        line-height: 16px;
        text-align: center;
    }
    .square {
        background-color: red;
        color: white;
        font-size: 10px;
        font-weight:bold;
        position: relative;
        top: -15px;
        border: 5px solid red;
        border-radius: 15px;
    }
    
.banner {
    height: 530 px;
    width: 100%;
    background: url('https://demo.themeies.com/silon/images/bg-1.jpg');
    display: flex;
    // flex-direction: column;
    background-size: cover;
   
    h2 {
        display: block;
        margin-bottom: 15px;
        font-weight: 500;
        line-height: 0.8;
        letter-spacing: 2.8px;
        text-transform: uppercase;
    }
    
    h1 {
    font-size: 40px;
    line-height: 56px;
    margin-bottom: 35px;
    }

    button {
    background-color: #f15151;
    border-radius: 3px;
    color: #fff;
    text-transform: uppercase;
    display: inline-block;
    font-size: 14px;
    font-weight: 500;
    line-height: 0.8;
    min-width: 170px;
    padding: 20px 25px;
     }
    }

    > div {
        width: 50%;
    }


    img {
        height: auto;
        width: 100%;

    }

